body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
	margin: 0 auto;
	max-width: 90vw;
}
.badge {
	padding: 5px 10px;
	margin-right: 10px;
	font-size: 14px;
    font-weight: 600;
    line-height: 1.3;
}

.badge:last-of-type {
	margin-right: 0;
}

.user {
	margin-bottom: 20px;
	padding: 10px;
	border-bottom: 1px solid #eee;
}

.user__name {
	font-size: 24px;
	font-family: Roboto,"Helvetica Neue",sans-serif;
}

.user__wrapper {
	display: -webkit-flex;
	display: flex;
}

.user__column {
	-webkit-flex: 0 1 auto;
	        flex: 0 1 auto;
	margin-right: 20px;
}

.user__column:last-of-type {
	margin-right: 0;
}

.user__media {
	max-width: 200px;
}

.user__image {
	display: block;
	width: 100%;
}

.user__activity,
.user__details {
	margin-bottom: 20px;
}
@-webkit-keyframes flash {
	from {
		opacity: 1
	}

	to {
		opacity: 0
	}
}

@keyframes flash {
	from {
		opacity: 1
	}

	to {
		opacity: 0
	}
}

.loader {
	text-align: center;
}

.loader .dot {
	font-size: 21px;
	margin: 0 2px;
}

.loader .dot:nth-child(1) {
	-webkit-animation: flash .85s .5s ease-in-out infinite ;
	        animation: flash .85s .5s ease-in-out infinite ;
}

.loader .dot:nth-child(2) {
	-webkit-animation: flash .85s .90s ease-in-out infinite ;
	        animation: flash .85s .90s ease-in-out infinite ;
}

.loader .dot:nth-child(3) {
	-webkit-animation: flash .85s .180s ease-in-out infinite ;
	        animation: flash .85s .180s ease-in-out infinite ;
}
.repos {
	margin-bottom: 20px;
}

.repos__heading {
	font-size: 24px;
	font-family: Roboto,"Helvetica Neue",sans-serif;
}

.repos-list {
	font-family: Roboto,"Helvetica Neue",sans-serif;
}

.repos-list__item {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.repos-list__title {
	font-size: 14px;
	margin-bottom: 10px;
}

.repos-list__subtitle {
    font-size: 11px;
	color: #747474;
	margin-bottom: 5px;
}

.badge {
	font-size: 12px;
}

.search {
	padding: 10px;
	margin-bottom: 20px;
	border-bottom: 1px solid #eee;
}

.search__field {
	width: 200px;
}
